*{
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    background-color: rgb(255, 245, 226);
}

/* navbar */
nav{
    display: inline-flex;
    position: fixed;
    height: auto;
    width: 100%;
    z-index: 1;
}
.navtag{
    padding: 2px;
    margin: 2px;
    width: 100%;
    background-color: wheat;

}
.navtag h1{
    font-size: 1.4rem;
    font-weight: 800;
    margin-left: 30px;
    background-color: wheat;

    display: inline-flex;
}
.navbar{
    background-color:wheat;
    height: 3rem;
    align-items: center;
    display: flex;
    width: 100%;

}
.navbarlink{
    display: inline-block;
    list-style: none;
    padding: 10px;
    margin: 10px; 
    padding-right: 30px;
    background-color:wheat;
}
.navlink{
    background-color: wheat;
    margin: 4px;
    padding: 4px;
    font-size: 1.2rem;
    font-weight: 400;
    text-decoration: none;
    transition: .2s;
    cursor: pointer;
}
.navlink:hover{
    font-size: 1.3rem;
}

/* homepage */

.home{
    height: 100%;
    width: 100%;
    position:static;
    display: flex;
    
}
.home-page{
    margin: 3rem;
    padding: 3rem;

    height: 100%;
    width: 100%;
    font-size: 2rem;
    align-items: center;
    text-align: center;
}
.homelinks{
    margin: 6px;
    padding: 6px;
    align-items: center;
    display:block;
    text-align: center;
    width: 100%;

}
.homelink{
    text-decoration: none;
    margin: 4px;
    padding: 4px;
    font-size: 1.5rem;
    border: 1.5px solid black;
    border-radius: 15px;
    align-items: center;
    transition: .2s;
    cursor: pointer;
}
.homelink:hover{
    border:2px solid black;
    font-size: 1.6rem;
    background-color: wheat;
}


/* register page */

.register-page{
    text-align:center;
    position:static;
    display: block;
    justify-content: center;
    align-items: center;
  }
  .registerlink{
    background-color: wheat;
    justify-content: center;
    text-align: center;
    align-items: center;
    width: 100%;
    font-size: 1.5rem;
    transition: .2s;
    height: 3rem;

}
.registerlink-tag{
    text-decoration: none;
    background-color: wheat;
    width: 100%;
    line-height: 3rem;
    font-size: 1.5rem;
    transition: .2s;
    height: 3rem;
    margin-top: 40px;
    padding-top: 40px;
    cursor: pointer;
}
.registerlink-tag:hover{
    font-size: 1.6rem;
}
  .register-page h1{
    font-size: 40px;
    margin: 3px;
    padding: 3px;
  }

  .register-menu{
    margin: 6px;
    padding: 6px;
    align-items: center;
    text-align: center;
    line-height: 3rem;
  }
  
  .register-input{
    background-color:rgb(255, 248, 236);
    border: 1px solid white;
    border-radius: 60px;
    align-items: center;
    text-align: center;
    
    display: flex;
    flex-direction: column;
    margin: 6px;
    padding: 6px;
    font-size: 1.3rem;
    font-weight: 600;
}
.register-input input{
    height: 2.5rem;
    width: 15rem;
    font-size: 1.3rem;
    background-color: white;
    border-radius: 15px;
}
.register-input label{
    height: 2rem;
    margin: 4px;
    padding: 4px;
    line-height: 2rem;
    background-color:rgb(255, 248, 236);

}
.register-input button{
    height: 2.5rem;
    width: auto;
    margin: 8px;
    padding: 8px;
    font-size:1.2rem;
    font-weight: 800;
    align-items: center;
    border-radius: 10px;
    transition: .2s;
    cursor: pointer;

}
.register-input button:hover{
    font-size: 1.3rem;
    background-color: wheat;
}
/* todo/ */

.task-input{
    background-color:rgb(255, 248, 236);
    border: 1px solid white;
    border-radius: 60px;
    align-items: center;
    text-align: center;
    display: block;
    font-size: 1.3rem;
    font-weight: 600;
}
.task-input input{
    height: 2.6rem;
    width: 15rem;
    font-size: large;
    background-color: white;
    border-radius: 15px;
    align-items: center;
}

.task-input button{
    height: auto;
    width: auto;
    margin: 8px;
    padding: 8px;
    font-size:1.2rem;
    font-weight: 800;
    align-items: center;
    border-radius: 30px;
    transition: .2s;
    cursor: pointer;
}
.task-input button:hover{
    font-size: 1.3rem;

}
.addedtask{
    align-items: center;
    text-align: center;

    display: inline-flex;
    font-size: 1.2rem;
    margin: 2px;
    padding: 2px;

}
.addedtask li{
    list-style: none;
    margin: 2px;
    padding: 2px;
    text-decoration: none;
}

/* footer */
.footer-section{
    width: 100%;
    height: 4rem;
    margin: 0;
    display: inline;
    padding: 0;
}
footer{
    margin-top: 20px;
    padding-top: 20px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    font-size: 1.3rem;
    font-weight: 400;
    color: var(--main-color);
    text-align: center;
    background-color: var(--bg-color);
}
.delete{
    background-color: red;
    size:4rem;
}